<template>
  <div>
        <nav class="navbar navbar-expand-sm navbar-light fixed-top itdmnav">
            <div class="container">
                <div class="navbar-header">

                    <div class="d-inline-flex" @click.prevent="backtrack">
                        <button class="btn  mr-1" ><i class="fa fa-arrow-left fa-lg" aria-hidden="true" ></i></button>
                        <div class="minilogo"></div>
                    </div>

                </div>

            </div>


        </nav>
<main style="height: 900px;">
    <div class="content_wrapper" style="align-items: start;">
        <div class="job_wrapper col col-sm-8 col-md-6  col-lg-3 col-xl-4 container-fluid container-sm">
            <form class="job_form col">
    <p class="error">{{ error }}</p>    
    <qrcode-stream @decode="onDecode" @init="onInit" />
    <p class="decode-result">: <b>{{ result }}</b><b :class="{'hidden': prompt}">{{ prompttext }}</b></p>
        <div class="row">
            <div class="col" :class="{'hidden': isshow}">
                <label>{{ tempscanlist[tempindex].inspectname }}</label>
                <div v-if="tempscanlist[tempindex].inspecttype === '0'">
                    <div class="form-check form-check-inline">
                    <input class="form-check-input" type="radio" name="inlineRadioOptions" id="inlineRadio1" value="Y" v-model="lookresult">
                    <label class="form-check-label" for="inlineRadio1">Ok</label>
                    </div>
                    <div class="form-check form-check-inline">
                    <input class="form-check-input" type="radio" name="inlineRadioOptions" id="inlineRadio2" value="N" v-model="lookresult">
                    <label class="form-check-label" for="inlineRadio2">Fail</label>
                    </div>
                </div>
                <div v-else-if="tempscanlist[tempindex].inspecttype === '1'">
                 <input type="number" step="0.1" class="form-control" v-model="lookresult">
                </div>
                <div v-else-if="tempscanlist[tempindex].inspecttype === '2'">
                    <div class="form-group">
                        <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" v-model="lookresult"></textarea>
                    </div>
                </div>
                <button id="btn1" class="btn btn-primary btn-block" @click.prevent="scandone"></button>
                <div class="d-flex justify-content-center mt-4">
                <div class="error-message alert text-center w-50" style="position: flex;" :class="{'hidden': prompt}">
                    {{ prompttext }}
                </div>
                </div>
            </div>
        </div>
    </form>
        </div>        
    </div>
      </main>
  </div>
</template>

<script>
import { QrcodeStream } from '../../src'
import { mapGetters, mapActions } from 'vuex';
export default {

  components: { QrcodeStream },

  data () {
    return {
      result: '',
      error: '',
      lookresult:'',
      tempscanlist: [],
      tempindex:'0',
      isshow:true,
      prompt:true,
      prompttext:''
    }
  },

  methods: {
    onDecode (result) {
        const vm = this;
        vm.result = result;
        const findex = vm.tempscanlist.findIndex(item => item.inspectno === result);
        vm.tempindex = findex;
        vm.isshow = false;
    },
    async onInit (promise) {
      try {
        await promise
      } catch (error) {
        if (error.name === 'NotAllowedError') {
          this.error = "ERROR: you need to grant camera access permisson"
        } else if (error.name === 'NotFoundError') {
          this.error = "ERROR: no camera on this device"
        } else if (error.name === 'NotSupportedError') {
          this.error = "ERROR: secure context required (HTTPS, localhost)"
        } else if (error.name === 'NotReadableError') {
          this.error = "ERROR: is the camera already in use?"
        } else if (error.name === 'OverconstrainedError') {
          this.error = "ERROR: installed cameras are not suitable"
        } else if (error.name === 'StreamApiNotSupportedError') {
          this.error = "ERROR: Stream API is not supported in this browser"
        }
      }
    },
    backtrack () {
        const vm = this;
        vm.$router.push('/ScanList')
    },
    scandone () {
      const vm = this;
      if (vm.lookresult === '') {
        vm.prompttext = ''
        vm.prompt = false;
      } else {
        const tempscan = [];
        const createts = new Date(+new Date() + 8 * 3600 * 1000).toISOString().split('.')
        tempscan.push(vm.tempindex);
        tempscan.push(vm.lookresult);
        tempscan.push(vm.currentuser.userid);
        tempscan.push(vm.currentuser.username);
        tempscan.push(createts[0]);
        tempscan.push(vm.currentuser.fullname);
        vm.scandonetemp(tempscan);
        vm.prompttext = '';
        vm.prompt = false;
        vm.lookresult = '';
        vm.isshow = true;
      }
    },
    ...mapActions(['scandonetemp']),
  },
  watch: {
      prompt: function () {
      const vm = this;
      setTimeout(() => {
          vm.prompt = true;
      }, 3000);
      }
  },
  computed:{
      ...mapGetters(['scanlist', 'currentuser'])
  },
  created() {
    const vm = this;
    vm.tempscanlist = vm.scanlist;
  },
  }
</script>

<style scoped>
.error {
  font-weight: bold;
  color: red;
}
</style>